.float-card-v1 {
  // margin: 0 10npx 16npx 10npx;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 12npx 16npx;
  background-color: #fff;
  border-radius: 4npx;
  .top-contain {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 12npx;
  }
  .draw-app-icon {
    margin-right: 12npx;
    width: 58npx;
    height: 58npx;
    border-radius: 12npx;
  }
  .card-intro {
    margin: 0 12npx 0 0;
    flex: 1;
    max-height: 75npx;
    overflow: hidden;
  }

  h1 {
    margin: 0 8npx 0 0;
    font-size: 16npx;
    line-height: 20npx;
    font-weight: 500;
    color: #303030;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    flex-shrink: 1;
  }
  .title-contain {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 4npx;
    margin-right: 12npx;
  }
  .app-desc {
    display: -webkit-box;
    font-size: 14npx;
    line-height: 18npx;
    text-align: left;
    color: #777;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }

  .close-btn {
    position: absolute;
    right: 10npx;
    top: 10npx;
    width: 15npx;
    height: 15npx;
    background-image: url('../../../../assets/imgs/close@3x.png');
    background-repeat: no-repeat;
    background-size: contain;
  }
  .draw-button {
    width: 100%;
    height: 40npx;
    border-radius: 4npx;
    font-weight: 500;
    text-align: center;
  }
  .draw-button .wrap {
    border-radius: 8npx;
    font-size: 16npx;
    font-weight: 300;
    background: rgba(0, 64, 202, 0.96);
  }
}
