.ad-union-icon.with-npx {
  position: fixed;
  display: flex;
  align-items: center;
  height: 16npx;
  padding-left: 3npx;
  padding-right: 3npx;
  background: rgba(0, 0, 0, 0.15);
  border-radius: 2npx;
  &.withoutBg {
    background: unset;
  }

  &.leftTop {
    top: 16npx;
    left: 14npx;
  }

  &.leftBottom {
    left: 10npx;
    bottom: 10npx;
  }

  &.rightBottom {
    right: 10npx;
    bottom: 10npx;
  }

  &.imageLeftBottom {
    position: absolute;
    left: 8npx;
    bottom: 8npx;
  }

  &.cardLeftTop {
    position: fixed; // 解决iphonex html不贴底边的问题
    left: 16npx;
    bottom: 98npx;
  }

  &.titleRight {
    position: inherit;
    flex-shrink: 0;
  }
  &.video-leftBottom {
    left: 12npx;
    bottom: 12npx;
  }
  &.video-leftBottom-hori {
    left: 16npx;
    bottom: 8npx;
  }
  .aglin {
    display: flex;
    align-items: flex-end;
    // width: 150npx;
    .icon {
      background-size: cover;
      width: 12npx;
      height: 12npx;
    }
    .margin-right-4 {
      margin-right: 4npx;
    }
    .iconWhite {
      background-size: cover;
      width: 12npx;
      height: 12npx;
      margin-right: 4npx;
      background-image: url(./img/adMarkIcon.png);
    }
    .iconGray {
      background-size: cover;
      width: 12npx;
      height: 12npx;
      margin-right: 4npx;
      background-image: url(./img/adGrayMarkIcon.png);
    }
    .white {
      font-size: 10npx;
      font-family: PingFangSC, PingFangSC-Light;
      font-weight: 300;
      text-align: center;
      color: #ffffff;
      opacity: 0.6;
      line-height: 12npx;
    }
    .gray {
      font-size: 10npx;
      font-family: PingFangSC, PingFangSC-Light;
      font-weight: 300;
      text-align: center;
      color: #9c9c9c;
      line-height: 12npx;
    }
  }
}
