.app-icon.with-npx {
  &.normal {
    width: 100npx;
    height: 100npx;
    border-radius: 20npx;
  }

  &.small {
    width: 80npx;
    height: 80npx;
    border-radius: 16npx;
  }

  &.vertical-v3 {
    border: 1px solid #fff;
  }

  &.smaller {
    width: 58npx;
    height: 58npx;
    border-radius: 12npx;
  }
  &.size-70 {
    width: 70npx;
    height: 70npx;
    border-radius: 11npx;
  }
  &.size-60 {
    width: 60npx;
    height: 60npx;
    border-radius: 11npx;
  }
  &.size-69 {
    width: 69npx;
    height: 69npx;
    border-radius: 10npx;
  }
  &.size-75 {
    width: 75npx;
    height: 75npx;
    border-radius: 4npx;
  }
  &.size-50 {
    width: 50npx;
    height: 50npx;
    border-radius: 8npx;
  }
  &.size-40 {
    width: 40npx;
    height: 40npx;
    border-radius: 10npx;
  }
  &.size-65 {
    width: 65npx;
    height: 65npx;
    border-radius: 12npx;
  }
}
