@primaryColor: #fe3666;
.text (@size, @color, @lineHeight: 1, @fontWeight: 400) {
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: @fontWeight;
  font-size: @size;
  color: @color;
  line-height: @lineHeight;
}

.textEllipsis(@clamp: 1) {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: @clamp;
}
.px2vw(@name; @value; @base: 375) {
  @{name}: round(@value * 100 / @base, 2) * 1vw;
}

/** 基于设计稿总宽度270换算vw */
.px2vw-270(@name,@value) {
  .px2vw(@name, @value, 270);
}

/** 基于设计稿总宽度375换算vw */
.px2vw-375(@name,@value) {
  .px2vw(@name, @value, 375);
}

.border-1px(@color: #ccc, @direction: bottom, @style: solid, @radius: 0) {
  position: relative;
  &::after {
    content: '';
    pointer-events: none;
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    transform-origin: 0 0;
    border-@{direction}: 1px @style @color;
    border-radius: @radius;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    @media (min-resolution: 2dppx) {
      width: 200%;
      height: 200%;
      border-radius: @radius * 2;
      transform: scale(0.5);
    }
    @media (min-resolution: 3dppx) {
      width: 300%;
      height: 300%;
      border-radius: @radius * 3;
      transform: scale(0.333);
    }
  }
}
