.app-icon {
  &.normal {
    width: 200mpx;
    height: 200mpx;
    border-radius: 40mpx;
  }

  &.small {
    width: 160mpx;
    height: 160mpx;
    border-radius: 32mpx;
  }

  &.vertical-v3 {
    border: 1px solid #fff;
  }

  &.smaller {
    width: 116mpx;
    height: 116mpx;
    border-radius: 24mpx;
  }
  &.size-70 {
    width: 140mpx;
    height: 140mpx;
    border-radius: 22mpx;
  }
  &.size-60 {
    width: 120mpx;
    height: 120mpx;
    border-radius: 22mpx;
  }
  &.size-69 {
    width: 138mpx;
    height: 138mpx;
    border-radius: 20mpx;
  }
  &.size-75 {
    width: 150mpx;
    height: 150mpx;
    border-radius: 8mpx;
  }
  &.size-58 {
    width: 116mpx;
    height: 116mpx;
    border-radius: 24mpx;
  }
  &.size-50 {
    width: 100mpx;
    height: 100mpx;
    border-radius: 16mpx;
  }
  &.size-40 {
    width: 80mpx;
    height: 80mpx;
    border-radius: 20mpx;
  }
  &.size-65 {
    width: 130mpx;
    height: 130mpx;
    border-radius: 24mpx;
  }
}
