.app-icon.with-wpx {
  &.normal {
    width: 200wpx;
    height: 200wpx;
    border-radius: 40wpx;
  }

  &.small {
    width: 160wpx;
    height: 160wpx;
    border-radius: 32wpx;
  }

  &.vertical-v3 {
    border: 1px solid #fff;
  }

  &.smaller {
    width: 116wpx;
    height: 116wpx;
    border-radius: 24wpx;
  }
  &.size-70 {
    width: 140wpx;
    height: 140wpx;
    border-radius: 22wpx;
  }
  &.size-60 {
    width: 120wpx;
    height: 120wpx;
    border-radius: 22wpx;
  }
  &.size-69 {
    width: 138wpx;
    height: 138wpx;
    border-radius: 20wpx;
  }
  &.size-75 {
    width: 150wpx;
    height: 150wpx;
    border-radius: 8wpx;
  }
  &.size-50 {
    width: 100wpx;
    height: 100wpx;
    border-radius: 16wpx;
  }
  &.size-40 {
    width: 80wpx;
    height: 80wpx;
    border-radius: 20wpx;
  }
  &.size-65 {
    width: 130wpx;
    height: 130wpx;
    border-radius: 24wpx;
  }
}
