.ad-union-icon.with-wpx {
    position: fixed;
    display: flex;
    align-items: center;
    height: 32wpx;
    padding-left: 6wpx;
    padding-right: 6wpx;
    background: rgba(0, 0, 0, 0.15);
    border-radius: 4wpx;
    &.withoutBg {
        background: unset;
    }

    &.leftTop {
        top: 32wpx;
        left: 28wpx;
    }

    &.leftBottom {
        left: 20wpx;
        bottom: 20wpx;
    }

    &.rightBottom {
        right: 20wpx;
        bottom: 20wpx;
    }

    &.imageLeftBottom {
        position: absolute;
        left: 16wpx;
        bottom: 16wpx;
    }

    &.cardLeftTop {
        position: fixed; // 解决iphonex html不贴底边的问题
        left: 32wpx;
        bottom: 196wpx;
    }

    &.titleRight {
        position: inherit;
        flex-shrink: 0;
    }
    &.video-leftBottom {
        left: 24wpx;
        bottom: 24wpx;
    }
    &.video-leftBottom-hori {
        left: 32wpx;
        bottom: 16wpx;
    }
    .aglin {
        display: flex;
        align-items: flex-end;
        .icon {
            background-size: cover;
            width: 24wpx;
            height: 24wpx;
        }
        .margin-right-4 {
            margin-right: 8wpx;
        }
        .iconWhite {
            background-size: cover;
            width: 24wpx;
            height: 24wpx;
            margin-right: 8wpx;
            background-image: url(./img/adMarkIcon.png);
        }
        .iconGray {
            background-size: cover;
            width: 24wpx;
            height: 24wpx;
            margin-right: 8wpx;
            background-image: url(./img/adGrayMarkIcon.png);
        }
        .white {
            font-size: 20wpx;
            font-family: PingFangSC, PingFangSC-Light;
            font-weight: 300;
            text-align: center;
            color: #ffffff;
            opacity: 0.6;
            line-height: 24wpx;
        }
        .gray {
            font-size: 20wpx;
            font-family: PingFangSC, PingFangSC-Light;
            font-weight: 300;
            text-align: center;
            color: #9c9c9c;
            line-height: 24wpx;
        }
    }
}
