@import '~@/common/less/mixins.less';
.button {
  padding: 0;
  font-weight: 500;
  line-height: 1;
  color: #fff;
  border: none;
  outline: none;
  overflow: hidden;
  .wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    font-size: 100%;
    position: relative;
    overflow: hidden;
  }
  &.twirl-button {
    -webkit-animation: twirlIn 0.8s linear 0.12s infinite normal both;
    -webkit-transform-origin: 50% 50%;
    animation: twirlIn 0.8s linear 0.12s infinite normal both;
    transform-origin: 50% 50%;
  }
  &.beat-button {
    -webkit-animation: beat 0.8s linear 0.12s infinite normal both;
    -webkit-transform-origin: 50% 50%;
    animation: beat 0.8s linear 0.12s infinite normal both;
    transform-origin: 50% 50%;
  }
  &.beatdouble-button {
    -webkit-animation: doubleButtonBeat 0.6s cubic-bezier(0.25, 0.1, 0.25, 0.1) infinite normal both;
    -webkit-transform-origin: 50% 50%;
    animation: doubleButtonBeat 0.6s cubic-bezier(0.25, 0.1, 0.25, 0.1) infinite normal both;
    transform-origin: 50% 50%;
  }
  &.flash-button {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      left: -130px;
      top: -30px;
      width: 250px;
      height: 40px; /**光标的宽度，可根据实际调整**/
      background-image: linear-gradient(transparent, rgba(255, 255, 255, 0.3));
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      transform: rotate(-45deg);
      animation: buttonFlash 2s cubic-bezier(0.42, 0, 1, 1) infinite normal both; /**第一个数字参数控制扫光速度，数字越大越慢**/
      z-index: 1;
    }
  }
  &.breathe-button {
    -webkit-animation: breathe 0.6s cubic-bezier(0.5, 0, 0.3, 1) infinite normal both;
    -webkit-transform-origin: 50% 50%;
    animation: breathe 0.6s cubic-bezier(0.5, 0, 0.3, 1) infinite normal both;
    transform-origin: 50% 50%;
  }
}
.btn-text {
  position: absolute !important;
  left: 0;
  text-align: center;
  width: 100%;
  opacity: 0;
  &.static {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: none;
    opacity: 1;
    position: static !important;
    transform: translateY(0);
  }
  .textEllipsis();
}
.normal-text {
  &.text-animation {
    animation: textBeat 4s ease-out infinite normal both;
    transform-origin: 50% 50%;
  }
}
.stimulate-text {
  &.text-animation {
    animation: textBeatReverse 4s ease-out infinite normal both;
    transform-origin: 50% 50%;
  }
}
.progress-bar {
  position: absolute;
  height: 200%;
  left: 0;
  top: -50%;
  z-index: -1;
}
@keyframes twirlIn {
  0% {
    transform: rotateZ(0deg);
  }
  10% {
    transform: rotateZ(6deg);
  }
  20% {
    transform: rotateZ(0deg);
  }
  30% {
    transform: rotateZ(-6deg);
  }
  40% {
    transform: rotateZ(0deg);
  }
  50% {
    transform: rotateZ(5deg);
  }
  60% {
    transform: rotateZ(-4deg);
  }
  65% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}

