  }
  32.14% {
    transform: scaleX(1.002) scaleY(1.002);
  }
  33.88% {
    transform: scaleX(1.039) scaleY(1.039);
  }
  37.36% {
    transform: scaleX(1.026) scaleY(1.026);
  }
  39.1% {
    transform: scaleX(1.003) scaleY(1.003);
  }
  40.84% {
